.Node {
  height: 25px;
  width: 25px;
  border: 1px solid black;
}

.node-start {
  background: green;
}

.node-finish {
  background: red;
}

.node-wall {
  animation-name: createWall;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.node-path {
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes pathAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(0, 190, 218, 0.75);
    border-radius: 100%;
  }
  50% {
    transform: scale(0.3);
    background-color: rgba(0, 218, 0, 0.75);
    border-radius: 100%;
  }
  75% {
    transform: scale(0.3);
    background-color: rgba(164, 218, 0, 0.75);
    border-radius: 100%;
  }
  100% {
    transform: scale(0.3);
    background-color: rgba(255, 255, 0, 0.892);
    border-radius: 100%;
  }
}

.node-visited {
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}
@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 0.75);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}
@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 254, 106);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(255, 254, 106);
  }

  100% {
    transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}
@keyframes createWall {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 255, 255);
  }

  100% {
    transform: scale(1);
    background-color: rgb(60, 60, 60);
  }
}
